<template>
  <div class="channel-edit">
    <!--? 我的频道 -->
    <van-cell title="我的频道">
      <template #default>
        <van-button
        class="edit-btn"
        round
        size="mini"
        type="danger"
        plain
        @click="isEditShow = !isEditShow"
        >
          {{isEditShow ? '完成':'编辑'}}
        </van-button>
      </template>
    </van-cell>

    <!--? 我的频道选项 -->
    <!--! 为什么动态属性active要以索引来确定那个频道高亮的 -->
    <!--? 答: 因为父组件中的active是以索引来确定那个频道的高亮的 -->
    <van-grid :gutter="10" class="my-grid">
      <van-grid-item
      v-for="(value,index) in myChannels"
      :key="value.id"
      :text="value.name"
      class="channel-item"
      icon="close"
      :class="{active: active === index}"
      @click="onMyChannel(value,index)"
      >
      <template #icon>
        <van-icon name="close" v-show="isEditShow && index !==0 "></van-icon>
      </template>
      </van-grid-item>
    </van-grid>

    <!--? 频道推荐 -->
    <van-cell title="频道推荐"></van-cell>

    <!--? 频道推荐选项 -->
    <van-grid :gutter="10" class="recommend-grid">
      <van-grid-item
      :text="value.name"
      class="channel-item"
      v-for="value in recommendChannels_lodash"
      :key="value.id"
      icon="plus"
      @click="onAddChannel(value)"
      />
    </van-grid>
  </div>
</template>

<script>
/*
  目标: 处理推荐频道,让我的频道 + 推荐频道 = 全部频道
  => 数组过滤(来筛选出,推荐频道)
  => 该写在哪? - 当我的频道,或者全部频道发生变化时,就需要去对推荐频道进行一次新的筛选
  => computed 计算属性
*/
/*
  目标: 通过频道推荐,将新的频道添加到我的频道中
  => 将点击的元素push到myChannels数组中
  => 为什么只改变了我的频道的列表数据,但是推荐频道的数据也在改变 => 当计算属性中所使用的变量发生改变时,会自动的重新计算
*/

/*
  目标: 通过点击编辑按钮
  1. 显示隐藏和删除icon
  2. 推荐频道不可删除
  3. 编辑按钮会与完成按钮进行切换
*/

/*
  目标: 当非编辑状态时 - 实际切换频道
  1. 关闭弹出层
  2. 在home页面切换到选中的频道
*/

/*
  目标: 当在编辑状态时 - 实现频道的删除
  1. splice 删除数组中的某一元素 - 会改变原数组
  2. 当删除的元素的索引小于 当前选中的元素时, 需要让父组件的active - 1
  3. 当删除的元素的索引等于 当前选中的元素时, 需要让父组件的active - 1
  4. 如果删除的是推荐,那么禁止执行后续逻辑
*/
import { mapState } from 'vuex'
import { setItem } from '../../../utils/storage.js'
import { addUserChannelAPI, deleteUserChannelAPI } from '../../../api/index.js'
export default {
  name: 'channel-edit',
  props: {
    myChannels: { // 当前用户频道
      type: Array,
      default: () => []
    },
    active: { // 当前选中频道
      type: Number,
      default: 0
    },
    allChannels: { // 所有频道列表
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      myChannelsClone: this.myChannels,
      isEditShow: false
    }
  },
  /*
    从store状态光利器中获取到state数据的两种方式
    如何选择:如果一个页面中频繁的会使用到state中的某几个属性时,使用方法2,可以煎炒推送变量的代码
            虽然会用到各种state中定义的变量,但每个变量的使用频率并不频繁,选择方法1
    1. this.$store.state
    2. ...
  */
  computed: {
    ...mapState(['user']),
    // 通过计算属性获取推荐列表内容
    recommendChannels () {
      // 通过filter遍历数组的方式,能够获取到每一个全部频道中的元素
      // 简单来说, 就是做了一次双重循环
      const newList = this.allChannels.filter(channel => {
        // 将该元素与我的频道列表中的元素做对比,如果我的频道有相同的元素id时,该元素被过滤掉
        /*
          查找数组中的某一特定元素的方法
          find - 返回查找到的那个元素,如果没找到返回undefined
          findIndex - 返回查找到的那个元素所在的索引,如果没找到返回 -1
          如果数组中的元素均为简单数据类型
          indexOf - 返回查找到的那个元素所在的索引, 如果没找到返回 -1
          includes - 判断数组中有无该元素, 有返回true,无返回false
        */
        return !this.myChannels.find(myChannels => {
          return myChannels.id === channel.id
        })
      })
      return newList
    },
    // 使用lodash的方法来完成过滤操作
    recommendChannels_lodash () {
      return this.$_.differenceBy(this.allChannels, this.myChannels, 'id')
    }
  },
  methods: {
    // 添加频道
    async onAddChannel (newChannel) {
      this.myChannelsClone.push(newChannel)

      // 数据持久化处理
      if (this.user) {
        // 已登录状态
        try {
          // 我们只关心接口有没有调用成功,并不关心返回值的内容 -不需要res
          // 方法一
          // await addUserChannelAPI({
          //   channels: this.myChannels.map((channel, index) => {
          //     channel.seq = index
          //     return channel
          //   })
          // })
          // 方法二
          const channel = [
            {
              id: newChannel.id,
              seq: this.myChannels.length - 1
            }
          ]
          await addUserChannelAPI({
            channels: channel
          })
          this.$toast('添加成功')
        } catch (error) {
          this.$toast('添加失败')
        }
      } else {
        // 未登录状态,把数据存到本地
        setItem('TOUTIAO_CHANNELS', this.myChannels)
      }
    },
    // 点击我的频道中的方法
    onMyChannel (channel, index) {
      // 1. 区分是编辑状态还是非编辑状态
      if (this.isEditShow) {
        // 判断是否为推荐
        if (index === 0) return
        // 编辑状态 - 删除频道
        // 删除选中的频道
        this.myChannelsClone.splice(index, 1)
        // 让激活的频道的索引active-1
        if (index <= this.active) {
          this.$emit('update-active', this.active - 1, true)
        }
        // 删除数据持久化
        this.deleteChannel(channel)
      } else {
        // 非编辑状态 - 切换频道
        this.$emit('update-active', index)
      }
    },
    async deleteChannel (delchannel) {
      // 登录状态
      if (this.user) {
        try {
          await deleteUserChannelAPI(delchannel.id)
        } catch (error) {
          this.$toast('删除频道失败')
        }
      } else {
        setItem('TOUTIAO_CHANNELS', this.myChannels)
      }
    }
  }
}
</script>

<style lang="less" scoped>
.channel-edit {
  .van-cell__title {
    height: 58px;
    line-height: 58px;
    font-size: 32px;
    color: #333;
  }

  .edit-btn {
    width: 100px;
    height: 48px;
    line-height: 48px;
    font-size: 26px;
    color: #f85959;
    border: 1px solid #f85959;
  }
  .channel-item {
    height: 86px;
    width: 160px;
    // 设置方块背景颜色
    :deep(.van-grid-item__content) {
      // flex布局的横向布局
      // 所有的子元素不会进行换行
      white-space:nowrap;
      flex-direction: row;
      background-color: #f5f5f6;

      // 设置方块字体的样式
      .van-grid-item__text {
        color: #222;
        font-size: 28px;
        margin-top: 0;
      }
    }
  }

  // 我的频道 close 图标的样式处理
  :deep(.my-grid) {
    .van-grid-item__content {
      .van-grid-item__icon-wrapper {
        position: absolute;
        top: -10px;
        right: -10px;
        font-size: 30px;
        color: #cacaca;
        z-index: 2;
      }
    }
    // 设置动态高亮样式
    .active {
      .van-grid-item__text {
        color: red;
      }
    }
  }

  // 频道推荐 '+' 图标的样式处理
  :deep(.recommend-grid) {
    .van-grid-item__content {
      // // flex布局的横向布局
      // // 所有的子元素不会进行换行
      // white-space:nowrap;
      // flex-direction: row;

      .van-icon-plus {
        font-size: 28px;
        margin-right: 6px;
      }
    }
  }
}
</style>
