<template>
  <div class="home-container">
    <!--? 标题 -->
    <van-nav-bar class="page-nav-bar" fixed>
      <template #title>
        <van-button
        class="search-btn"
        round
        icon="search"
        type="info"
        to="/search"
        >搜索</van-button>
      </template>
    </van-nav-bar>
    <!--? tab频道列表 -->
    <!--
        ? v-model:绑定当前激活标签对应的索引值,默认启动第一个标签
        ? animated: 开启切换标签内容时的转场动画
        ? swipeable: 开启滑动切换标签页 (一体现在移动端)
        ? swipe-threshold: 设置最低标准,越低越好
    -->
    <van-tabs class="channel-tabs" v-model="active" animated swipeable swipe-threshold="2">
      <van-tab
      v-for="obj in channels"
      :key="obj.id"
      :title="obj.name"
      >
      <!--? 中间放一个组件 -->
        <ArticleList :channel="obj"></ArticleList>
      </van-tab>
      <template #nav-right>
        <!--? 创建以下小盒子的目的 -占位,通过透明盒子的站位,让最右侧的标签图标不会遮挡滚动的标签样式 -->
        <div class="placeholder"></div>
        <div class="hamburger-btn" @click="isChannelEditShow = true">
          <i class="toutiao toutiao-gengduo"></i>
        </div>
      </template>
    </van-tabs>

    <!--? 频道编辑底部弹出 -->
    <van-popup
    class="edit-channel-popup"
    v-model="isChannelEditShow"
    closeable
    close-icon-position="top-left"
    :style="{height:'100%'}"
    position="bottom"
    >
    <!--?子组件-->
    <ChannelEdit
    :myChannels="channels"
    :active="active"
    :allChannels="allChannels"
    @update-active="onUpdateActive"
    />
    </van-popup>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getUserChannelsAPI, getAllChannelsAPI } from '../../api/index.js'
import ArticleList from './components/ArticleList.vue'
import ChannelEdit from './components/ChannelEdit.vue'
import { getItem } from '../../utils/storage.js'
export default {
  name: 'home-vue',
  components: {
    ArticleList,
    ChannelEdit
  },
  data () {
    return {
      active: 0,
      channels: [], // 频道列表
      isChannelEditShow: false, // 底部频道编辑状态
      allChannels: [] // 全部频道列表
    }
  },
  computed: {
    ...mapState(['user'])
  },
  created () {
    // 调取获取频道列表的方法
    this.loadChannels()
    // 调用获取全部频道的方法
    this.loadAllChannels()
  },
  methods: {
    async loadChannels () {
      try {
        // if (this.user) {
        //   // 以后调用接口获取返回值是,请使用结构赋值的形式拿到真实的返回参数
        //   const { data: res } = await getUserChannelsAPI()
        //   // console.log(res)
        //   this.channels = res.data.channels
        // } else {
        //   // 未登录状态
        //   // 获取本地频道列表
        //   const localChannels = getItem('TOUTIAO_CHANNELS')
        //   if (localChannels) {
        //     // 有本地存储
        //     this.channels = localChannels
        //   } else {
        //     // 没有本地存储
        //     const { data: res } = await getUserChannelsAPI()
        //     this.channels = res.data.channels
        //   }
        // }

        // 优化上面的3种不同的判断
        let channelsTemp = [] // 临时列表数据,最后会将它赋值给this.channels
        const localChannels = getItem('TOUTIAO_CHANNELS') // 获取本地列表
        if (this.user || !localChannels) {
          // 登录或者本地没有存储时,获取后端数据
          const { data: res } = await getUserChannelsAPI()
          channelsTemp = res.data.channels
        } else {
          // 除上面条件以外,均可使用本地列表数据
          channelsTemp = localChannels
        }
        // 将临时列表赋值给真实列表
        this.channels = channelsTemp
      } catch (error) {
        this.$toast('获取频道列表数据失败')
      }
    },
    showPopup () {
      this.isChannelEditShow = true
    },
    async loadAllChannels () {
      try {
        const { data: res } = await getAllChannelsAPI()
        this.allChannels = res.data.channels
      } catch (error) {
        this.$toast('获取全部频道数据失败')
      }
    },
    // 自定义组件事件
    onUpdateActive (index, isShow = false) {
      // 1.修改active当前选中的频道
      this.active = index
      // 2. 将弹出层关闭
      this.isChannelEditShow = isShow
    }
  }
}
</script>

<style lang="less" scoped>
.home-container {
  padding-top: 174px;
  padding-bottom: 100px;
  /deep/.van-nav-bar__title {
    max-width: unset;
  }
  .search-btn {
    background-color: #5babfb;
    border: none;
    width: 555px;
    height: 64px;
    font-size: 28px;
    .van-icon {
      color: #fff;
      font-size: 28px;
    }
  }
  :deep(.channel-tabs) {
    .van-tabs__wrap {
      height: 82px;
      position: fixed;
      top: 92px;
      z-index: 1;
      left: 0;
      right: 0;
    }

    .van-tab {
      border-right:1px solid #edeff3;
      min-width: 200px;
      font-size: 30px;
      color: #777;
    }

    .van-tab--active {
      color: #333;
      font-weight: 700;
    }
    .van-tabs__line {
      width: 32px;
      background-color: #5babfb;
    }
    .placeholder {
      width: 66px;
      flex-shrink: 0; // flex布局需要用flex-shrink取消该元素在flex布局的计算
    }
    .hamburger-btn {
      position: fixed;
      right: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 66px;
      height: 82px;
      background-color: rgba(255 255 255);
      &::before {
        content: '';
        position: absolute;
        left: 0;
        width: 2px;
        height: 58px;
        background-image: url(../../assets/gradient-gray-line.png);
        background-size: contain;
      }
    }
  }
  // 弹出层样式
  .edit-channel-popup {
    padding-top: 100px;
    box-sizing: border-box;
  }
}
</style>
